/**
 * Override bootstrap modal styles
 * add close button position(inside and outside)
 */
.modal.d-none.show {
  display: block !important; }

.modal.modal-close-inside .close, .modal.modal-close-outside .close {
  cursor: pointer;
  outline: none;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 5;
  width: auto;
  height: auto;
  font-size: 16px;
  line-height: 1;
  transition: all 0.3s ease-in-out; }
  @media (min-width: 768px) {
    .modal.modal-close-inside .close, .modal.modal-close-outside .close {
      font-size: 24px;
      line-height: 1;
      top: 20px;
      right: 20px; } }

.modal .close {
  color: #000;
  opacity: 1 !important; }
  .modal .close:not(:disabled):not(.disabled) {
    color: #000; }
    .modal .close:not(:disabled):not(.disabled):hover, .modal .close:not(:disabled):not(.disabled):focus, .modal .close:not(:disabled):not(.disabled):active {
      color: #F8E5E1; }
  .modal .close:hover, .modal .close:focus, .modal .close:active {
    color: #F8E5E1; }

.modal.modal-close-outside .close {
  top: -20px; }

@media (max-width: 399.98px) {
  .modal.modal-close-outside .modal-dialog {
    margin-top: 20px; } }

.modal .modal-title {
  font: 500 24px/26px "Montserrat", sans-serif; }

/*
* @section      MODAL AS LEFT/RIGHT SIDEBAR
* @description  Add "left" or "right" in modal parent div, after class="modal".
*/
.modal.left .modal-dialog, .modal.right .modal-dialog {
  top: 0;
  position: fixed;
  margin: auto;
  width: 300px;
  height: 100%;
  transform: translate3d(0, 0, 0) !important; }
  @media (min-width: 768px) {
    .modal.left .modal-dialog, .modal.right .modal-dialog {
      width: 400px; } }

.modal.left .modal-content, .modal.right .modal-content {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; }
  .modal.left .modal-content::-webkit-scrollbar, .modal.right .modal-content::-webkit-scrollbar {
    width: 6px; }
  .modal.left .modal-content::-webkit-scrollbar-track, .modal.right .modal-content::-webkit-scrollbar-track {
    background: #f2f2f2;
    border-radius: 0; }
  .modal.left .modal-content::-webkit-scrollbar-thumb, .modal.right .modal-content::-webkit-scrollbar-thumb {
    background: #999999;
    transition: all 0.3s;
    border-radius: 0; }
    .modal.left .modal-content::-webkit-scrollbar-thumb:hover, .modal.right .modal-content::-webkit-scrollbar-thumb:hover {
      background: #F8E5E1; }

.modal.left.fade .modal-dialog {
  left: -300px;
  transition: opacity 0.3s linear, left 0.3s ease-out; }
  @media (min-width: 768px) {
    .modal.left.fade .modal-dialog {
      left: -400px; } }

.modal.left.fade.show .modal-dialog {
  left: 0; }

.modal.right.fade .modal-dialog {
  right: -300px;
  transition: opacity 0.3s linear, right 0.3s ease-out; }
  @media (min-width: 768px) {
    .modal.right.fade .modal-dialog {
      right: -400px; } }

.modal.right.fade.show .modal-dialog {
  right: 0; }

.modal-header {
  position: relative; }
  .modal-header .close {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0;
    margin: 0; }

/**
 * Styles for "product zoom" modal window
 * Used on product page and quick view popup
 */
#product-modal {
  background: white; }
  #product-modal .modal-dialog {
    margin-top: 0;
    margin-bottom: 0; }
    #product-modal .modal-dialog .modal-content {
      background: none;
      border: none;
      box-shadow: none; }
      #product-modal .modal-dialog .modal-content .modal-body {
        padding: 0; }
        #product-modal .modal-dialog .modal-content .modal-body .zoom-product-cover {
          position: relative; }
          #product-modal .modal-dialog .modal-content .modal-body .zoom-product-cover .btn {
            position: absolute;
            bottom: 10px;
            left: 50%;
            transform: translateX(-50%);
            padding: 0;
            width: 50px;
            height: 50px;
            text-align: center;
            letter-spacing: 0; }
            #product-modal .modal-dialog .modal-content .modal-body .zoom-product-cover .btn i {
              font-size: 24px;
              line-height: 48px; }
        #product-modal .modal-dialog .modal-content .modal-body #modalThumb {
          transition: none !important; }
          #product-modal .modal-dialog .modal-content .modal-body #modalThumb .swiper-container {
            padding-top: 5px; }
            #product-modal .modal-dialog .modal-content .modal-body #modalThumb .swiper-container .thumb-container img {
              cursor: pointer;
              background: #f8f8f8;
              box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0);
              transition: all 0.3s ease-in-out; }
              #product-modal .modal-dialog .modal-content .modal-body #modalThumb .swiper-container .thumb-container img.selected, #product-modal .modal-dialog .modal-content .modal-body #modalThumb .swiper-container .thumb-container img:hover {
                background: #fff;
                box-shadow: inset 0 0 0 1px black; }
              #product-modal .modal-dialog .modal-content .modal-body #modalThumb .swiper-container .thumb-container img.selected {
                pointer-events: none;
                cursor: default; }
          #product-modal .modal-dialog .modal-content .modal-body #modalThumb.collapse:not(.show) {
            max-height: 0;
            overflow: hidden; }
  #product-modal.show .modal-dialog {
    top: 50%;
    transform: translate(0, -50%); }

/**
