.daydeal-box {
  padding: 10px 5px;
  font-size: 12px;
  line-height: 1;
  color: #000;
  text-align: center;
  font-weight: normal;
  background: #F8E5E1; }
  .daydeal-box h3 {
    display: none; }
  .daydeal-box > div {
    display: flex;
    justify-content: center; }
    .daydeal-box > div > span {
      font-weight: 300;
      font-size: 13px;
      line-height: 1.3;
      font-family: "Montserrat", sans-serif;
      text-align: center;
      min-width: 43px;
      color: #fff; }
      .daydeal-box > div > span > span {
        display: block;
        font-size: 16px;
        letter-spacing: 0.15em; }
      .daydeal-box > div > span + span {
        margin-left: 5px; }
  @media (min-width: 768px) {
    .daydeal-box {
      padding: 10px 5px;
      min-width: 40px; }
      .daydeal-box > div > span > span {
        font-size: 18px; }
      .daydeal-box > div > span + span {
        margin-left: 10px; } }
  @media (min-width: 992px) {
    .daydeal-box {
      padding: 15px 5px; } }
  @media (min-width: 1200px) {
    .daydeal-box {
      padding: 25px 5px; }
      .daydeal-box > div > span {
        min-width: 43px; }
        .daydeal-box > div > span > span {
          font-size: 26px; }
        .daydeal-box > div > span + span {
          margin-left: 15px; } }
  @media (min-width: 1780px) {
    .daydeal-box > div > span > span {
      letter-spacing: 0.2em; }
    .daydeal-box > div > span + span {
      margin-left: 25px; } }

