.post-meta > div {
  font: 300 13px/16px "Montserrat", sans-serif;
  color: #000; }
  .post-meta > div em {
    font-style: normal; }
  .post-meta > div:not(:last-child) {
    margin-right: 20px; }

.bp-name, .bc-name {
  font: 500 24px/34px "Montserrat", sans-serif; }

.blog-post-wrapper {
  margin: 0 auto;
  max-width: 1200px; }

.blog-post-name {
  font: 300 34px/40px "Montserrat", sans-serif; }
  @media (min-width: 1200px) {
    .blog-post-name {
      font-size: 40px; } }

.post-description p strong {
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.15em; }

.post-description blockquote {
  padding: 15px 20px 5px;
  margin: 0 0 20px;
  border-left: 2px solid #F8E5E1; }
  .post-description blockquote p {
    font-size: 18px;
    line-height: 1.33; }

.post-tags {
  display: flex;
  align-items: center; }
  .post-tags h5 {
    text-transform: uppercase;
    letter-spacing: 0.15em; }
  .post-tags a {
    font: 400 14px/16px "Montserrat", sans-serif;
    letter-spacing: 0.15em; }
    .post-tags a + a {
      margin-left: 10px; }
