/**
 * Main Header styles
 */
#header {
  background: #fff; }
  #header .jxml-logo {
    display: inline-block; }
  #header .settings-toggle {
    cursor: pointer;
    font-size: 24px;
    line-height: 1;
    color: #000;
    transition: all 0.3s ease-in-out; }
    #header .settings-toggle:hover, #header .settings-toggle:focus, #header .settings-toggle:active {
      color: #F8E5E1; }
  #header .settings-wrapper .lang-wrapper, #header .settings-wrapper .currency-wrapper {
    width: 45%;
    display: inline-block; }
  #header .jxwishlist-link > a, #header .compare-header > a {
    text-transform: uppercase;
    letter-spacing: 0.3em;
    display: flex;
    align-items: center; }
    #header .jxwishlist-link > a i, #header .compare-header > a i {
      font-size: 20px;
      margin-right: 10px; }
  #header .link-block > ul > li a {
    font: 500 13px/16px "Montserrat", sans-serif;
    letter-spacing: 0.3em;
    color: #000;
    text-transform: uppercase; }
    #header .link-block > ul > li a:hover, #header .link-block > ul > li a:focus, #header .link-block > ul > li a:active {
      color: #F8E5E1; }
  #header .link-block > ul > li:not(:last-child) {
    margin-right: 36px; }
  @media (min-width: 768px) {
    #header {
      padding-bottom: 21px; } }

/**
 * Main WRAPPER styles
 * make layout's columns position
 */
#wrapper {
  flex: 1 1 auto; }
  body:not(#index) #wrapper {
    padding: 50px 0; }
  @media (min-width: 768px) {
    body#product #wrapper {
      padding-top: 80px; } }
  @media (min-width: 1200px) {
    body#product #wrapper {
      padding-top: 130px; } }
  #wrapper .left-column, #wrapper .right-column {
    flex: 0 0 100%; }
    @media (min-width: 1200px) {
      #wrapper .left-column, #wrapper .right-column {
        max-width: 300px;
        flex: 0 0 300px; } }
  @media (min-width: 768px) {
    #wrapper .left-column > *, #wrapper .right-column > * {
      margin-bottom: 40px; }
    #wrapper .left-column {
      order: 1; }
    #wrapper .content-wrapper {
      order: 2; }
    #wrapper .right-column {
      order: 3; } }
  @media (min-width: 1200px) {
    #wrapper .left-column > *, #wrapper .right-column > * {
      margin-bottom: 50px; } }

/**
 * Main FOOTER styles
 */
#footer {
  background: #fff; }
  #footer .border-top-black {
    border-top: solid 1px #000; }
  #footer .jxml-logo {
    display: inline-block; }
  #footer .footer-links ul > li {
    margin-bottom: 0; }
    #footer .footer-links ul > li a {
      display: inline-block;
      font: 400 13px/16px "Montserrat", sans-serif;
      color: #000;
      position: relative; }
      #footer .footer-links ul > li a:before {
        position: absolute;
        content: '';
        height: 1px;
        background: #000;
        top: 100%;
        left: 0;
        width: 0;
        transition: all 0.3s ease-in-out; }
      #footer .footer-links ul > li a:hover:before, #footer .footer-links ul > li a:focus:before, #footer .footer-links ul > li a:active:before {
        width: 100%; }
    #footer .footer-links ul > li + li {
      margin-top: 8px; }
  #footer .block-social > ul > li a i {
    font-size: 18px;
    line-height: 1; }
  #footer .block-social > ul > li + li {
    margin-left: 35px; }
  #footer .jxml-copyright {
    color: #8B8B8B;
    font: 400 14px/18px "Montserrat", sans-serif; }
    #footer .jxml-copyright a {
      color: #8B8B8B; }
      #footer .jxml-copyright a:hover, #footer .jxml-copyright a:focus, #footer .jxml-copyright a:active {
        color: #000; }

/**
 * Wishlist override styles
 * Fix urls for images
 */
