/**
 * Styles for 'page not found' block
 * Use on "404" page
 */
#pagenotfound {
  .page-not-found {
    background: url('data:image/png;base64,') no-repeat;
    background-size: contain;
    padding-left: 495px;
    min-height: 504px;
    margin: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .page-not-found-info {
      padding-left: 50px;
    }
    #search_widget {
      max-width: 350px;
    }
    @include media-breakpoint-down(xl) {
      padding-left: 300px;
      min-height: 305px;
      .page-not-found-info {
        padding-left: 30px;
      }
    }
    @include media-breakpoint-down(sm) {
      background-size: 320px;
      padding-left: 0;
      min-height: auto;
      padding-top: 326px;
      .page-not-found-info {
        padding-left: 0;
        padding-top: 30px;
      }
    }
  }
}