/*
* @section      Style for login wrapper
* @description  This section holds specific style redeclarations for
*               login content wrapper
*/
.jx-header-account > a {
  color: #000; }
  .jx-header-account > a i {
    font-size: 24px;
    line-height: 1; }
  .jx-header-account > a span {
    display: none; }
  .jx-header-account > a:hover, .jx-header-account > a:focus, .jx-header-account > a:active {
    color: #F8E5E1; }

.dropdown .jx-header-account-wrapper {
  padding: 30px;
  width: 320px; }
  @media (min-width: 768px) {
    .dropdown .jx-header-account-wrapper {
      width: 568px;
      padding: 65px 65px 50px; } }

@media (min-width: 768px) {
  .jx-header-account-wrapper.twocolumns .user-data {
    display: flex;
    align-items: center;
    margin-bottom: 20px; }
    .jx-header-account-wrapper.twocolumns .user-data img {
      flex: 0 0 30%;
      max-width: 30%;
      margin-right: 20px;
      border: 2px solid rgba(0, 0, 0, 0.2); }
    .jx-header-account-wrapper.twocolumns .user-data h3 {
      order: 1; }
  .jx-header-account-wrapper.twocolumns .header-account-content > ul {
    column-count: 2;
    column-gap: 20px;
    list-style-position: inside; } }

@media (max-width: 767.98px) {
  .jx-header-account-wrapper .user-data {
    text-align: center; }
    .jx-header-account-wrapper .user-data img {
      width: 70%; } }

@media (min-width: 576px) and (max-width: 767.98px) {
  .jx-header-account-wrapper .user-data img {
    width: 60%; } }

.jx-header-account-wrapper .header-login-content {
  display: none; }
  .jx-header-account-wrapper .header-login-content.active {
    display: block;
    max-height: none;
    overflow: visible; }

.jx-header-account-wrapper .social-login-buttons a {
  font-size: 20px;
  line-height: 1;
  color: #8B8B8B; }
  .jx-header-account-wrapper .social-login-buttons a:hover, .jx-header-account-wrapper .social-login-buttons a:focus, .jx-header-account-wrapper .social-login-buttons a:active {
    color: #000; }

.jx-header-account-wrapper footer {
  text-align: center; }
  .jx-header-account-wrapper footer .register-link {
    margin-top: 10px; }

.jx-header-account-wrapper .radio-inline {
  margin-right: 20px; }
  .jx-header-account-wrapper .radio-inline:last-child {
    margin-right: 0; }
  .jx-header-account-wrapper .radio-inline .custom-radio {
    margin-right: 0; }

/*
* @section      Style for login wrapper
* @description  This section holds specific style redeclarations for
*               login content wrapper
*/
#wrapper .jx-header-account-wrapper, #footer .jx-header-account-wrapper {
  padding: 20px;
  max-width: 100%;
  margin-bottom: 30px;
  background: #ffffff;
  box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, 0.2); }
  #wrapper .jx-header-account-wrapper [class^="col-"], #wrapper .jx-header-account-wrapper [class*=" col-"], #footer .jx-header-account-wrapper [class^="col-"], #footer .jx-header-account-wrapper [class*=" col-"] {
    width: 100%;
    flex: 0 0 100%;
    text-align: left; }

.main-help-block ul:empty {
  display: none; }

/**
* @section   Style for header account modal
 */
@media (min-width: 768px) {
  [id*='jxha-modal-']:not(.left):not(.right) .modal-body {
    padding: 65px 65px 50px; } }

@media (min-width: 768px) {
  [id*='jxha-modal-'].left .modal-body, [id*='jxha-modal-'].right .modal-body {
    padding: 50px 30px; } }
