@charset "UTF-8";
/**
 * Mixin for icons in css
 * Example1: @include addIcon('\f021', font-size, line-height, color, margin-left, margin-right, vertical-align)
 * Example2: @include addIcon('\f021', font-size, 20px, $grey-900, margin-left, margin-right, -3px);
 * Example3: @include addIcon('\f021', 14px, 20px, $grey-900, 10px, 5px, -1px);
 */
/**
 * Override fo "button-variant" default bootstrap's mixin
 */
.newsletter-overlay {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9999;
  overflow: hidden; }

.newsletter-box {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9999;
  overflow: hidden; }

/*popup styles*/
#newsletter_popup {
  max-width: 1022px;
  min-width: 300px;
  width: 90%;
  position: relative;
  margin: 10px auto; }
  @media (min-width: 768px) {
    #newsletter_popup {
      margin: 30px auto; } }

.jxnewsletter-inner {
  padding: 80px 50px;
  text-align: center; }
  @media (min-width: 576px) and (max-width: 767.98px) {
    .jxnewsletter-inner {
      padding: 140px 71px; } }
  @media (min-width: 768px) {
    .jxnewsletter-inner {
      max-width: 55.58%;
      flex: 0 0 55.58%; } }
  @media (min-width: 992px) {
    .jxnewsletter-inner {
      padding: 140px 71px; } }

/*header styles*/
.jxnewsletter-header h4, .jxnewsletter-header h5 {
  margin: 0; }

.jxnewsletter-header h4 {
  font-weight: 500;
  font-size: 26px;
  line-height: 1.35;
  font-family: "Montserrat", sans-serif;
  text-transform: capitalize; }

.jxnewsletter-header h5 {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.35;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  padding: 0 10%;
  display: none; }
  @media (min-width: 768px) {
    .jxnewsletter-header h5 {
      display: block; } }
  @media (min-width: 992px) {
    .jxnewsletter-header h5 {
      padding: 0 20%; } }

.jxnewsletter-header h5 + h4 {
  margin-top: 5px; }

/*content styles*/
.jxnewsletter-content {
  margin-top: 15px; }
  .jxnewsletter-content .description {
    margin-top: 10px;
    color: #8B8B8B;
    font: 300 18px/24px "Montserrat", sans-serif; }
  .jxnewsletter-content .input-group {
    flex-direction: column; }
    .jxnewsletter-content .input-group .form-control, .jxnewsletter-content .input-group .input-group-append .btn {
      width: 100%; }
    .jxnewsletter-content .input-group .form-control {
      text-align: center; }
    .jxnewsletter-content .input-group .input-group-append {
      margin: -1px 0 0 0; }

/*footer styles*/
.jxnewsletter-footer {
  overflow: hidden; }

/*submit button styles*/
/*close button styles*/
.icon.jxnewsletter-close {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  color: #8B8B8B;
  transition: all 0.3s ease-in-out; }
  .icon.jxnewsletter-close:before {
    font-family: 'Linearicons';
    content: "";
    font-size: 22px;
    line-height: 1; }
  @media (min-width: 992px) {
    .icon.jxnewsletter-close {
      right: 45px;
      top: 45px; }
      .icon.jxnewsletter-close:before {
        font-size: 24px; } }
  .icon.jxnewsletter-close:hover, .icon.jxnewsletter-close:focus, .icon.jxnewsletter-close:active {
    color: #000; }
